.medal {
    width: 220px;
    margin: 30px auto 0 auto;
    position: relative;
}

.medal .icon {
    position: absolute;
    top: calc(50% - 30px);
    left: -10px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #F7F5FA;
    border: 4px solid #cdcdcd;
    transform: scale(1.05);
}

.icon-img {
    max-width: 65%;
    position: relative;
    z-index: 10;
}

.medal .btn {
    text-align: left;
    padding: 3px 3px 3px 75px !important;
    font-size: 15px;
    font-weight: 700;
}

.medal .level {
    display: block;
    color: #fff;
    font-size: 13px;
    font-weight: 400;
}

/* SVG */
.svg-circle-container {
    position: absolute;
    width: 70px; height: 70px;
    top: calc(50% - 35px);
    left: -15px;
    z-index: 10;
}

.circle {
    stroke-width: 4;
    fill: transparent;
    stroke-dasharray: 190;
}

.circle.default { stroke: #F7F5FA; }
.circle.primary { stroke: #FF4B89; }
.circle.secondary { stroke: #29F39F; }
.circle.success { stroke: #8BF3FB; }
.circle.info { stroke: #FFFD7B; }
.circle.danger { stroke: #FE0022; }

.circle.per05 { animation: percent05 1.5s ease forwards; }
.circle.per10 { animation: percent10 1.5s ease forwards; }
